import {
  ImageButton,
  ImageBorder,
  ImageReset,
  ImageStyle,
} from './components/imageButton';
import isSelectionInImage from './utils/inSelectionInImage';
import locale from '../../locales/zh_CN';

export default function toolbar(imageConfig) {
  const imageLocaleConfig = imageConfig.locale || locale.image;
  return {
    buttons: {
      default: ImageButton,
      border: ImageBorder,
      reset: ImageReset,
      style: ImageStyle,
    },
    locale: imageLocaleConfig,
    getTarget(controller) {
      if (isSelectionInImage(controller)) {
        return 'image';
      }
      return undefined;
    },
  };
}
